import { mapGetters } from 'vuex'
import { setByKey } from '../../util/auth.js'
export default {
	data() {
		return {
			inspectionIndex: '1',
			inspectionItem:[]
		}
	},
	computed: {
		...mapGetters([
			'patrolTemplateType'
		])
	},
	onLoad() {
		this.inspectionItem = this.patrolTemplateType
	},
	methods: {
		RadioChange(e){
			this.inspectionIndex = e.detail.value
		},
		goScan(e){
			var _this = this
			setByKey('checkedTemplateType',_this.inspectionIndex)
			uni.navigateTo({
			    url: 'inspectionHeader'
			});
		},
		goLast(){
			var _this = this
			uni.navigateTo({
			    url: 'inspectionIndex'
			});
		}
	}
}
